import { Component, OnInit } from '@angular/core';
import { Http, Response} from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
@Component({
  selector: 'app-couponlist',
  templateUrl: './couponlist.component.html',
  styleUrls: ['./couponlist.component.css']
})
export class CouponlistComponent implements OnInit {
  closeResult: string;
  Id: number;
  CouponCode: string;
  Type: string;
  CouponsAvailable: number;
  CouponsReedemed: number;
  Value: number;

  // CouponList = [
  //   {Id : 1, CouponCode: 'Test01', Type: 'Two Bed', CouponsAvailable: 95, CouponsReedemed: 67, Value: 30},
  //   {Id : 2, CouponCode: 'Test01', Type: 'Two Bed', CouponsAvailable: 95, CouponsReedemed: 67, Value: 30},
  //   {Id : 3, CouponCode: 'Test01', Type: 'Two Bed', CouponsAvailable: 95, CouponsReedemed: 67, Value: 30},
  //   {Id : 4, CouponCode: 'Test01', Type: 'Two Bed', CouponsAvailable: 95, CouponsReedemed: 67, Value: 30},
  //   {Id : 5, CouponCode: 'Test01', Type: 'Two Bed', CouponsAvailable: 95, CouponsReedemed: 67, Value: 30},
  //   {Id : 6, CouponCode: 'Test01', Type: 'Two Bed', CouponsAvailable: 95, CouponsReedemed: 67, Value: 30},
  //   {Id : 7, CouponCode: 'Test01', Type: 'Two Bed', CouponsAvailable: 95, CouponsReedemed: 67, Value: 30}
  // ];

private apiUrl = 'http://localhost:56486/api/Default1';
data: any = {};

constructor(private http: Http, private modalService: NgbModal) {
   this.getData();
  }
  getData() {
    return this.http.get(this.apiUrl)
    .map((res: Response) => res.json())
     .subscribe(data => {
      console.log(data);
      this.data = data;
      });
  }

  onDeleteCoupon(id: number){
    console.log(id);
    if(confirm('Are you sure you want to delete this record ?') == true) {
      return this.http.delete(this.apiUrl + id)
      .map(data =>data.json()).toPromise();
    }
  }


  open(content) {
    this.modalService.open(content).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  ngOnInit() {
  }

}



